import { Observable } from 'rxjs';
import { map, withLatestFrom } from 'rxjs/operators';

import { Database, HTTPResponse } from './database-base';

export interface SubjectEntry {
  subjectCode?: string;
  subjectName?: string;
  downloadLink?: string;
}

export class StudentDatabase extends Database {
  constructor(protected readonly server: string) {
    super(server);
  }

  getNews(): Observable<HTTPResponse<string>> {
    return this.get(`StuReg/BindIndex`).pipe(
      map((response) => {
        return response;
      })
    );
  }

  getPersonalInfo(): Observable<HTTPResponse<string>> {
    return this.get(`StuReg/Stu_Wj_Info`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getPayment(): Observable<HTTPResponse<string>> {
    return this.get(`StuReg/Stu_Pay`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getReceipts(): Observable<HTTPResponse<string>> {
    return this.get(`StuReg/Query_Stu_Jf`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getScoresRecheckInfo(): Observable<HTTPResponse<string>> {
    return this.get(`StuTk/BindCjfc`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getRegistration(): Observable<HTTPResponse<string>> {
    return this.post(`StuSK/BindSKBkResult`).pipe(
      withLatestFrom(this.post(`StuSK/SkOrderDetail`)),
      map((responses) => {
        return {
          ok: responses.every((item) => item.ok),
          body: responses.map((item) => this.formatDOM(item.body)).join(''),
          status: responses[0].status
        };
      })
    );
  }

  getRegistrationInfo(): Observable<HTTPResponse<string>> {
    return this.post(`StuSk/GetPrintSkZKzInfo`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getScores(): Observable<HTTPResponse<string>> {
    return this.post(`StuSK/BindSKCj`).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const contentTable = doc.querySelector('table');

          contentTable.className = 'table table-bordered';
          response.body = contentTable.outerHTML;
        }
        return response;
      })
    );
  }

  getProcessAssessmentRegistration(): Observable<HTTPResponse<string>> {
    return this.post(`StuGckh/BindBkResult`).pipe(
      withLatestFrom(this.post(`StuSK/SkOrderDetail`)),
      map((responses) => {
        return {
          ok: responses.every((item) => item.ok),
          body: responses.map((item) => this.formatDOM(item.body)).join(''),
          status: responses[0].status
        };
      })
    );
  }

  getProcessAssessmentRegistrater(): Observable<HTTPResponse<string>> {
    return this.post(`StuGckh/BindBk`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getProcessAssessmentEditCourses(): Observable<HTTPResponse<string>> {
    return this.post(`StuGckh/BindGkkhbkModify`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getProcessAssessmentEditLogs(): Observable<HTTPResponse<string>> {
    return this.post(`StuGckh/BindChangeKcHis`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getProcessAssessmentScores(): Observable<HTTPResponse<string>> {
    return this.post(`StuGckh/BindGckhCj`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getGraduationOperationFlow(): Observable<HTTPResponse<{sources: Array<string>}>> {
    return this.post(`StuBiye/OperateProcess`).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const imgs = doc.querySelectorAll('img');
          const srcs: Array<string> = [];

          imgs.forEach((item) => {
            srcs.push(`${this.server}${item.getAttribute('src')}`);
          });
          response.body = {
            sources: srcs
          };
        }
        return response;
      })
    );
  }

  getDissertationUploadDate(): Observable<HTTPResponse<string>> {
    return this.post(`StuBiye/LwDate`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getGraduationStudySource(): Observable<HTTPResponse<{ source: string }>> {
    return this.post(`StuBiye/Kj`).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const link = doc.querySelector('a');

          response.body = {
            source: link.href
          };
        }
        return response;
      })
    );
  }

  getGraduationStudyProgress(): Observable<HTTPResponse<string>> {
    return this.post(`StuBiye/KjPro`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getDissertationUploadContent(): Observable<HTTPResponse<string>> {
    return this.post(`StuBiye/UploadStuBiyeInfo`).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');

          doc.querySelector('form').style.display = '';
          (doc.querySelector('#dv2') as HTMLElement).style.display = '';
          response.body = doc.body.innerHTML;
        }
        return response;
      })
    );
  }

  getDissertationAudit(): Observable<HTTPResponse<string>> {
    return this.post(`StuBiye/BindAuditInfo`).pipe(
      map((response) => {
        response.ok && (response.body = this.formatDOM(response.body));
        return response;
      })
    );
  }

  getOtherAnswers(): Observable<HTTPResponse<{results: Array<SubjectEntry>}>> {
    return this.post(`StuTk/DownLoadOther_BindData`).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const rows = (doc.querySelectorAll('a') as NodeListOf<HTMLAnchorElement>);
          const results: Array<SubjectEntry> = [];
          const server = this.server;

          rows.forEach((row) => {
            results.push({
              subjectCode: '',
              subjectName: row.textContent,
              downloadLink: `${server}` + row.getAttribute('href')
            });
          });
          response.body = {
            results: results
          };
        } else {
          response.body = {
            results: []
          };
        }
        return response;
      })
    );
  }

  searchAnswers(keyWord: string): Observable<HTTPResponse<{results: Array<SubjectEntry>}>> {
    return this.post(`StuTk/DownLoadSTDaAn_BindData`, JSON.stringify({kc: keyWord})).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const rows = (doc.querySelectorAll('table tr') as NodeListOf<HTMLTableRowElement>);
          const results: Array<SubjectEntry> = [];
          const server = this.server;

          rows.forEach((row) => {
            const cells = row.querySelectorAll('td');

            results.push({
              subjectCode: cells.item(0).textContent,
              subjectName: cells.item(1).textContent,
              downloadLink: `${server}` + cells.item(2).querySelector('a').getAttribute('href')
            });
            if (cells.item(5).querySelector('a')) {
              results.push({
                subjectCode: cells.item(3).textContent,
                subjectName: cells.item(4).textContent,
                downloadLink: `${server}` + cells.item(5).querySelector('a').getAttribute('href')
              });
            }
          });
          response.body = {
            results: results
          };
        } else {
          response.body = {
            results: []
          };
        }
        return response;
      })
    );
  }

  searchExams(keyWord: string): Observable<HTTPResponse<{results: Array<SubjectEntry>}>> {
    return this.post(`StuTk/DownLoadTK_BindData`, JSON.stringify({kc: keyWord})).pipe(
      map((response) => {
        if (response.ok) {
          const parser = new DOMParser();
          const doc = parser.parseFromString(response.body, 'text/html');
          const rows = (doc.querySelectorAll('table tr') as NodeListOf<HTMLTableRowElement>);
          const results: Array<SubjectEntry> = [];
          const server = this.server;

          rows.forEach((row) => {
            const cells = row.querySelectorAll('td');

            results.push({
              subjectCode: cells.item(0).textContent,
              subjectName: cells.item(1).textContent,
              downloadLink: `${server}` + cells.item(2).querySelector('a').getAttribute('href')
            });
            if (cells.item(5).querySelector('a')) {
              results.push({
                subjectCode: cells.item(3).textContent,
                subjectName: cells.item(4).textContent,
                downloadLink: `${server}` + cells.item(5).querySelector('a').getAttribute('href')
              });
            }
          });
          response.body = {
            results: results
          };
        } else {
          response.body = {
            results: []
          };
        }
        return response;
      })
    );
  }

  formatDOM(content: string): string {
    const parser = new DOMParser();
    const doc = parser.parseFromString(content, 'text/html');
    const contentTables = doc.querySelectorAll('table') as NodeListOf<HTMLTableElement>;
    const actionButtons = doc.querySelectorAll('input[type="button"]') as NodeListOf<HTMLInputElement>;

    contentTables.forEach((table) => {
      table.className = 'table table-bordered';
    });
    actionButtons.forEach((btn) => {
      btn.className = 'btn btn-primary';
    });
    return doc.body.innerHTML;
  }
}
