import { Observable } from 'rxjs';
import { fromFetch } from 'rxjs/fetch';

const DEFAULT_HEADERS: HeadersInit = {
  'Content-Type': 'application/json; charset=utf-8'
};

export interface HTTPOptions {
  headers?: HeadersInit;
  responseType?: 'json' | 'text';
}

export interface HTTPResponse<R> {
  body: R;
  ok?: boolean;
  status?: number;
}

export abstract class Database {
  constructor(protected readonly server: string) {

  }

  get(url: string, options?: HTTPOptions): Observable<HTTPResponse<any>> {
    return fromFetch(`${this.server}/${url}`, {
      method: 'GET',
      credentials: 'include',
      headers: {
        ...DEFAULT_HEADERS,
        ...options?.headers
      },
      redirect: 'error',
      selector: (response) => {
        return this.defaultSelector(response, options);
      }
    });
  }

  post(url: string, data?: BodyInit | null, options?: HTTPOptions): Observable<HTTPResponse<any>> {
    return fromFetch(`${this.server}/${url}`, {
      method: 'POST',
      body: data,
      credentials: 'include',
      headers: {
        ...DEFAULT_HEADERS,
        ...options?.headers
      },
      redirect: 'error',
      selector: (response) => {
        return this.defaultSelector(response, options);
      }
    });
  }

  private defaultSelector(response: Response, options?: HTTPOptions): Observable<HTTPResponse<any>> {
    const resp: HTTPResponse<any> = {
      ok: true,
      body: null,
      status: response.status
    };
    console.log(response);

    return new Observable((obs) => {
      if (response.ok) {
        let result: Promise<any>;

        switch (options?.responseType) {
          case 'json':
            result = response.json();
            break;
          default:
            result = response.text();
        }
        result.then((value) => {
          resp.body = value;
          obs.next(resp);
          obs.complete();
        }).catch((reason) => {
          resp.ok = false;
          resp.body = reason;
          obs.next(resp);
          obs.complete();
        });
      } else {
        switch (options?.responseType) {
          case 'json':
            resp.body = {};
            break;
          default:
            resp.body = '';
        }
        resp.ok = response.status !== 502;
        obs.next(resp);
        obs.complete();
      }
    });
  }
}

