import 'bootstrap';

import { createApp } from 'vue';
import { createPinia } from 'pinia';
import { useTitle } from '@vueuse/core';

import { ENV_SERVER } from './database/server';
import { AUTH_KEY, Authorization, authorizationStore } from './authorization/authorization';
import { TITLE_KEY } from './title';
import App from './App.vue';
import router from './router';
import { Loading } from '@/directives/loading';
import { Tooltip } from '@/directives/tooltip';
import Icon from '@/components/Icon.vue';
import ValidationMessage from '@/components/ValidationMessage.vue';

const server = 'http://202.115.44.69:90';
const auth = new Authorization(server);
const app = createApp(App);
const title = useTitle('', {
  titleTemplate: '%s | Sichuan University'
});

router.beforeEach(async (to, from) => {
  const signedIn = await auth!.checkSignInStatus().toPromise();
  const user = authorizationStore().user;

  user.isAuthenticated = signedIn;
  if (signedIn && to.name === 'sign-in') {
    return { path: 'system' };
  } else if (!signedIn && to.name !== 'sign-in') {
    return { name: 'sign-in' };
  } else {
    return true;
  }
});

app.use(createPinia());
app.use(router);

app.provide(ENV_SERVER, server);
app.provide(AUTH_KEY, auth);
app.provide(TITLE_KEY, title);

app.directive('loading', new Loading());
app.directive('tooltip', new Tooltip());
app.component('Icon', Icon);
app.component('ValidationMessage', ValidationMessage);
app.mount('#app');
