<script setup lang="ts">
  import { fromEvent } from 'rxjs';
  import { debounceTime, filter, finalize, map, startWith, switchMap, tap } from 'rxjs/operators';
  import { inject, onMounted, ref } from 'vue';
  import { useSubscription } from '@vueuse/rxjs'

  import { ENV_SERVER } from '@/database/server';
  import { SubjectEntry, StudentDatabase } from '@/database/student-database';

  const server = inject(ENV_SERVER) as string;
  const studentDatabase = new StudentDatabase(server);
  const ing = ref<boolean>(false);
  const searchEntry = ref<HTMLInputElement>(null);
  const results = ref<Array<SubjectEntry>>([]);
  const clearSearchBar = (): void => {
    searchEntry.value.value = '';
    searchEntry.value.dispatchEvent(new InputEvent('input'));
  };

  onMounted(() => {
    useSubscription(fromEvent(searchEntry.value, 'input').pipe(
      startWith({
        target: searchEntry.value
      }),
      debounceTime(300),
      map((event) => (event.target as HTMLInputElement).value),
      tap(() => {
        ing.value = true;
      }),
      switchMap((value) => {
        return studentDatabase.searchAnswers(value).pipe(
          finalize(() => {
            ing.value = false;
          })
        );
      })
    ).subscribe((response) => {
      results.value = response.body.results;
    }));
  });
</script>

<template>
  <div class="card border-0">
    <div class="card-header sticky-top">
      <div class="position-relative">
        <input type="text" class="form-control" placeholder="" ref="searchEntry" />
        <button class="btn btn-icon position-absolute top-0" type="button" @click="clearSearchBar()">
          <Icon name="close" />
        </button>
        <button class="btn btn-icon position-absolute top-0 end-0" type="button">
          <Icon name="search" />
        </button>
      </div>
    </div>
    <div class="card-body" v-loading="ing">
      <ul class="p-0 row">
        <li class="px-3 py-2 col-12 col-md-6 col-lg-4 col-xl-3 col-xxl-2" v-for="(item, index) of results" :key="index">
          <div class="border rounded px-2 py-1 h-100 hstack gap-2">
            <span>{{item.subjectCode}}| {{item.subjectName}}</span>
            <a class="ms-auto" :href="item.downloadLink" v-tooltip title="">
              <Icon class="align-middle" name="cloud_download" />
            </a>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  .btn-icon.position-absolute {
    border-radius: $input-border-radius;
  }

  .btn-icon.position-absolute:not(:last-child) {
    right: calc(24px + 0.5rem);
  }

