<script setup lang="ts">
  import { finalize } from 'rxjs/operators';
  import { inject, ref } from 'vue';

  import { ENV_SERVER } from '@/database/server';
  import { StudentDatabase } from '@/database/student-database';

  const server = inject(ENV_SERVER) as string;
  const studentDatabase = new StudentDatabase(server);
  const ing = ref<boolean>(true);
  const content = ref<string>('');
  const formatContent = (value: string): string => {
    return value.replace(/<li/g, '<li class="mb-2"').replace(/<a/g, '<a class="" ');
  };

  studentDatabase.getNews().pipe(
    finalize(() => {
      ing.value = false;
    })
  ).subscribe((response) => {
    content.value = formatContent(response.body);
  });
</script>

<template>
  <div class="card border-0">
    <div class="card-body" v-loading="ing">
      <div v-html="content"></div>
    </div>
  </div>
</template>

