import { DirectiveBinding, ObjectDirective, VNode } from 'vue';

const LOADINGS = new Map<HTMLElement, HTMLElement>();

const createLoading = (el: HTMLElement, binding: DirectiveBinding, vnode: VNode): void => {
  const content = document.createElement('div');
  const classNames = el.classList;

  if (binding.value && !LOADINGS.get(el)) {
    const theme = vnode.props?.['data-bs-theme'] || 'primary';

    if (classNames.contains('btn')) {
      const bar = document.createElement('div');

      if (!classNames.contains('btn-loading')) {
        el.className += ' btn-loading';
      }
      content.className = 'progress position-absolute rounded-0 h-100';
      content.style.left = '0px';
      content.style.right = '0px';
      content.style.bottom = '0px';
      content.style.zIndex = '0';
      bar.className = `progress-bar progress-bar-striped progress-bar-animated bg-${theme} w-100`;
      bar.setAttribute('role', 'progressbar');
      content.appendChild(bar);
    } else {
      const spinner = document.createElement('div');
      const label = document.createElement('span');

      content.className = 'd-flex justify-content-center align-items-center p-2';
      spinner.className = `spinner-border text-${theme}`;
      spinner.setAttribute('role', 'status');
      label.className = 'visually-hidden';
      label.textContent = 'Loading...';
      spinner.appendChild(label);
      content.appendChild(spinner);
    }
    el.appendChild(content);
    LOADINGS.set(el, content);
  } else if (!binding.value) {
    disposeLoading(el);
  }
};

const disposeLoading = (el: HTMLElement): void => {
  LOADINGS.get(el) && el.removeChild(LOADINGS.get(el)!);
  LOADINGS.delete(el);
  el.className = el.className.replace(/(^|\s)btn\-loading($|\s)/g, '');
};

export class Loading implements ObjectDirective {

  created(el: HTMLElement, binding: DirectiveBinding, vnode: VNode): void {
    createLoading(el, binding, vnode);
  }

  updated(el: HTMLElement, binding: DirectiveBinding, vnode: VNode): void {
    createLoading(el, binding, vnode);
  }

  unmounted(el: HTMLElement): void {
    disposeLoading(el);
  }
}
