import { InjectionKey } from '@vue/runtime-dom';
import { defineStore } from 'pinia';
import { useLocalStorage } from '@vueuse/core';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

import { Database } from '@/database/database-base';
import router from '@/router';
import { ISystemUser, IUser } from './user';

export class Authorization extends Database {
  constructor(protected readonly server: string) {
    super(server);
  }

  signIn(user: IUser, isAdmin?: boolean): Observable<boolean> {
    return this.post(`Account/StuLogin`, JSON.stringify({
      xs_bm: user.id,
      xs_xm: user.name
    })).pipe(
      map((response) => true),
      switchMap((result) => {
        const params = new URLSearchParams({
          xs_sfz: user.id!,
          sfz_flag: isAdmin ? '0' : '1'
        });

        return result ? this.post(`Account/ZyList`, params.toString(), {
          headers: {
            'Accept': 'text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,*/*;q=0.8',
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).pipe(
          catchError(() => of(null)),
          map(() => true)
        ) : of(false);
      })
    );
  }

  logout(): Observable<boolean> {
    return this.post(`Account/LoginOut`).pipe(
      map((response) => {
        response.ok && router.push({ path: '/sign-in', replace: true });
        return response.ok!;
      })
    );
  }

  checkSignInStatus(): Observable<boolean> {
    return this.post(`StuReg/BindIndex`).pipe(
      catchError((response) => {
        return of({
          body: false,
          ok: false,
          status: 0
        });
      }),
      map((response) => {
        return typeof response.body === 'string' ? !response.body.includes('<script>') : response.body;
      })
    );
  }
}

export const AUTH_KEY = Symbol() as InjectionKey<Authorization>;

export const authorizationStore = defineStore('authorization', {
  state: (): ISystemUser => ({
    name: useLocalStorage('auth-user-name', ''),
    isAuthenticated: false
  } as unknown as ISystemUser),
  getters: {
    user: (state: ISystemUser): ISystemUser => state
  }
});
